.quote {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    border-top: 3px solid #fff;
    border-bottom: 3px solid #fff;

    .quote-container {
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 0 2rem;

        h1 {
            text-align: center;
            font-size: 3rem;
            word-wrap: break-word;
            color: #fffdef;
            font-weight: 200;
            font-family: 'Parisienne', cursive;
        }
    }
}

@media screen and (max-width: 768px) {
    .quote {
        .quote-container {
            h1 {
                font-size: 2.25rem;
            }
        }
    }
}

@media screen and (max-width: 450px) {
    .quote {
        .quote-container {
            padding: 0 .75rem;

            h1 {
                font-size: 2rem;
            }
        }
    }
}